import React, { Component } from "react";
import Tone from "tone";
import SoundMachineInstrument from "./SoundMachineInstrument";
import { accentTypes } from "./AccentTypes";

class SoundMachine extends Component {
	instrumentTypes = {
		TABLA: "tabla",
		ELECTRO: "electro",
		SYNTH: "synth"
	};

	beatsPerStep = 3;

	accentNotes = ["C3", "C#3", "D3"]; // this stay in sync with AccentTypes

	state = {
		//isInitialized: false,
		instrument: this.instrumentTypes.TABLA,
		//beatsPerStep: 3,
		isPlaying: false
	};

	accents = [accentTypes.ACCENT_1]; // default with accent on first beat
	samples = {};

	instruments = {};

	part = null;

	constructor(props) {
		super(props);
		if (!props.beatsPerStep) {
			throw new Error("props.beatsPerStep is missing");
		}
		//  this.state.beatsPerStep = props.beatsPerStep;
		this.beatsPerStep = props.beatsPerStep;

		// initialize accents array with ticks
		// for (let i = 0 ; i < props.beatsPerStep ; i++) {
		//     this.accents.push({
		//          idx: i,
		//          type: accentTypes.ACCENT_2
		//     })
		// }
		// this.refreshAccentsArray(props.beatsPerStep);

		// set accent on first beat
		// this.accents[0].type =  accentTypes.ACCENT_1
	}

	// initAccentsArray() {
	//     // initialize accents array with ticks
	//     console.log('initAccentsArray()')
	//     // for (let i = 0 ; i < this.state.beatsPerStep ; i++) {
	//     for (let i = 0 ; i < this.beatsPerStep ; i++) {
	//         this.accents.push({
	//              idx: i,
	//              type: accentTypes.ACCENT_2
	//         })
	//     }
	//     // set accent on first beat
	//     this.accents[0].type =  accentTypes.ACCENT_1
	// }

	componentDidMount() {
		// reload/debug bell
		var synth = new Tone.Synth().toMaster();
		synth.triggerAttackRelease("A2");

		Tone.Buffer.on("load", () => this.restart());

		var tabla = new SoundMachineInstrument({
			key: "tabla",
			samples: ["dha-slide.wav", "dhin-slide.wav", "tin.wav"]
		}).toMaster();

		this.instruments[this.instrumentTypes.TABLA] = tabla;

		var electro = new SoundMachineInstrument({
			key: "machine",
			samples: ["Kick.wav", "HiHat.wav", "Snare.wav"]
		}).toMaster();

		this.instruments[this.instrumentTypes.ELECTRO] = electro;

		Tone.context.latencyHint = "playback";

		// TODO: dispatch ready event, so we can enable UI elements
	}

	setInstrument(instrumentKey) {
		// TODO validate key
		console.log('<SoundMachine>new instrument', instrumentKey)
		this.setState({ instrument: instrumentKey }, this.restart);
	}

	setBpm = bpm => {
		Tone.Transport.bpm.value = bpm;
	};

	setBeatsPerStep = newBps => {
	   
        this.beatsPerStep = newBps;
        
        console.log("<SoundMachine>setBeatsPerStep", newBps)
        
        this.restart()

		//     this.restart();
		// this.setState({beatsPerStep: newBeatsPerStep}, this.restart);
	};

	restart = () => {
		// check accents array so we know what to play
		// this.refreshAccentsArray()

		console.log("<SoundMachine>restart()");

		var sampler = this.instruments[this.state.instrument];

		if (this.part) {
			console.log("clearing this.part");
			this.part.stop()
			this.part.removeAll()
			this.part.dispose();
			// console.log("disposed this.part", this.part);
			this.part = undefined; // maybe it will help with garbage collection?
		}

		let score = [];
		// init with ACCENT_2 note, it will be changed in setAccents
		// for (let i = 0 ; i < this.state.beatsPerStep; i++) {
		for (let i = 0; i < this.beatsPerStep; i++) {
			score.push(["0:" + i, this.accentNotes[accentTypes.ACCENT_2]]);
		}

		console.log("new score ", score);

		this.part = new Tone.Part(function(time, note) {
			// trigger note
			sampler.triggerAttackRelease(note, "4n", time);
		}, score);

		// setting accents
		// console.log("this.part", this.part);
		// debugger
		this.setAccents(this.accents);

		// set signature
		console.log('setting timeSignature',this.beatsPerStep)
		Tone.Transport.timeSignature = [this.beatsPerStep, 4];
		// this.part.humanize = true; // this one is interesting
		this.part.loopEnd = "1m"; // + this.state.beatsPerStep;
		this.part.loop = true;

		// if (this.state.isPlaying === true ) {
			console.log('transpor was playing so play part')
			this.part.start(0);
		// }
	};

	// setAccents(accentIdxArray) {
	//     this.accents = accentIdxArray;
	//     for (let i = 0 ; i < this.part.length; i++) {
	//         this.part.at("0:"+i, this.accents.indexOf(i) < 0 ? this.tickNote: this.accentNote);
	//     }
	// }
 
	setAccents(accentTypesArr) {
		// if (accentTypesArr.length != this.part.length) {
		//     throw new Error("accentTypes has incorrect number of elements " + accentTypesArr.length + " vs " + this.part.length)
		// }
		this.accents = accentTypesArr;

		for (let i = 0; i < this.part.length; i++) {
			// by default 2 will be the tick note
			let note = this.accentNotes[accentTypes.ACCENT_2];

			// if accent for this step is specified set it here
			if (
				accentTypesArr[i] !== undefined &&
				accentTypesArr[i] == accentTypes.ACCENT_1
			) {
				note = this.accentNotes[accentTypes.ACCENT_1];
			} else if (
				accentTypesArr[i] !== undefined &&
				accentTypesArr[i] == accentTypes.ACCENT_3
			) {
				note = this.accentNotes[accentTypes.ACCENT_3];
			}
			this.part.at("0:" + i, note);
			// this.part.at("0:"+i, this.accents.indexOf(i) < 0 ? this.tickNote: this.accentNote);
		}
	}

	render() {
		return (
			<div>
				Sound is {this.state.isPlaying === true ? "playing" : "stopped"}
			</div>
		);
	}

	// toggle() {
	//     if (Tone.Transport.state === "started") {
	//         this.stop();
	//     }
	//     else {
	//         this.start()
	//     }
	// }

	stop() {
		console.log("<SoundMachine>stop()");
		this.setState({ isPlaying: false });
		//Tone.Transport.stop();
		Tone.Transport.stop();
		// if (this.part) {
		// 	this.part.stop();
		// }
	}

	start() {
		console.log("<SoundMachine>start()");

		// Tone.Transport.start("+0.05");
		this.setState({ isPlaying: true });
		// debugger
		Tone.Transport.start("+0.1");
		this.restart();
		// if (this.part) {
		// 	this.part.start();
		// }
		// if (this.part) {
		// 	this.part.start("+0.1");
		// }
	}

	getBpm = () => {
		return Tone.Transport.bpm.value;
	};
}

export default SoundMachine;
