import React, { Component } from "react";
import { Button, ButtonGroup } from "reactstrap";
import { accentTypes } from "./AccentTypes";


class BarManager extends Component {

	state = {
		bars: []
	};


	accents = [accentTypes.ACCENT_1,accentTypes.ACCENT_2,accentTypes.ACCENT_3];
	constructor(props) {
		super(props);		
		this.state.bars = this.prepareBars(props.defaultValue);
		
		// by def set accent on first beat
		this.state.bars[0].type = accentTypes.ACCENT_1;
	}

	prepareBars(amount) {
		
		let oldVal = this.getValue();

		let  bars = [];
		for (let i = 0; i < amount; i++) {
			bars.push({
				idx: i,
				type: (oldVal[i]  !== undefined) ? oldVal[i] : accentTypes.ACCENT_2
			});
		}
		return bars;
	}

	setBarsAmount(amount) {
		let bars = this.prepareBars(amount);
		// set state and send onAfterChange once updated as they have changed surely (at least the amount)
		this.setState({ bars: [...bars] },  this.props.onAfterChange);
	}

	handleCellClick(selected) {
		// this.bars[selected]
		let  bar = this.state.bars[selected]
		
		const type = bar.type;

		let newType = this.accents[(type + 1 ) % 3];
		
		this.state.bars[selected].type = newType;
		this.forceUpdate();



		/// hmm good patern to update arrays in state, doesn't want to lose it 
		// const index = this.state.accentBars.indexOf(selected);
		// if (index < 0) {
		// 	this.state.accentBars.push(selected);
		// } else {
		// 	this.state.accentBars.splice(index, 1);
		// }
		// this.setState({ accentBars: [...this.state.accentBars] });
	}

	getValue() {
		var bars = []
		for (let i = 0 ; i < this.state.bars.length ; i ++) {
			bars.push(this.state.bars[i].type)
		}
		return bars;
	}

	getBtnColor(type) {
		var color;
		switch (type) {
			case accentTypes.ACCENT_2:
				color = "secondary";
				break;
			case accentTypes.ACCENT_3:
				color = "warning";
				break;
			default:
				color = "primary"
		}
		return color;
	}
	renderCells = bars => {
		return bars.map(bar => (
			<Button
				size="sm"
				 color={this.getBtnColor(bar.type)}
				// className="btn-state-1"
				key={"barkey_" + bar.idx}
				onClick={() => this.handleCellClick(bar.idx)}
				// active={!this.state.accentBars.includes(bar)}
			>
				{bar.idx + 1}
			</Button>
		));
	};

	render() {
		// dunno how this works but it does the job bubbling up the click event
		const { onAfterChange } = this.props;

		return (
			<ButtonGroup onClick={onAfterChange}>
				{this.renderCells(this.state.bars)}
			</ButtonGroup>
		);
	}
}

export default BarManager;
