  totalStepsChanged = () => {    
		const s = this.getUiState();
		if (s.totalSteps !== this.state.totalSteps) {
			this.refs.planner.setPlan(this.makePlan(s));
		}
	}

	onBeatsPerStepChange = () => {
		// plan doesn't change here, just the beats per step amount
		// console.log("<App>onBeatsPerStepChange")
		const s = this.getUiState();
		// debugger
		// update barManager with newMeasureSetting
		if (s.beatsPerStep !== this.refs.sm.beatsPerStep) {
			this.refs.barManager.setBarsAmount(s.beatsPerStep);
			this.refs.sm.setBeatsPerStep(s.beatsPerStep);
			return;
		}
	}

	onBpmRangeChange() {
		const s = this.getUiState();
		if (s.bpmRange[0] !== this.state.bpmRange[0] || s.bpmRange[1] !== this.state.bpmRange[1]) {
			console.log('bpm range changed')
		
			console.log(s, this.state)
			this.setState({bpmRange: s.bpmRange});
		
			this.refs.planner.setPlan(this.makePlan(s));
		}
		
	}
	
	exerciseDurationChanged = () => {
		// make and execute new plan
		let newPlan = this.makePlan(this.getUiState());
		this.refs.planner.setPlan(newPlan);
	}

	onPlanAdvanced = (newStep) => {
		if (newStep) {
		console.log('<App>plan advanced, setting new bpm', newStep.bpm)
			this.setBpm(newStep.bpm);
		}
		else {
			// no step so just stop planner and transport
			this.refs.planner.stop();
			this.refs.sm.stop();
		}
	}

	setBpm = (newBpm) => {
		this.setState({ currentBpm: newBpm });
	 	this.refs.sm.setBpm(newBpm);
	}

	onPlanChanged = () => {
		// console.log("<App>onPlanChanged");
		
		let bar = this.refs.planner.getCurrentBar() 
		// set initial speed
		if (bar) {
			this.setBpm(bar.bpm);
		}
  };
  
  onInstrumentChange() {
		this.setState(prevState => ({
			instrumentDropdownOpen: !prevState.instrumentDropdownOpen
		}));
  }
  
  onCycleLockChange() {
    this.setState(prevState => ({
			cycleDropdownOpen: !prevState.cycleDropdownOpen
		}));
  }

	handleStartStop = () => {
		// if stop then stop 
		console.log("<App>handleStartStop, isPlaying=",this.refs.sm.state.isPlaying )
		if (this.refs.sm.state.isPlaying === true) {

			this.refs.planner.stop();
			this.refs.sm.stop();
			// reset bpm
			
		}
		else {
			this.refs.planner.start();
			this.refs.sm.start();
		}
	}



