	makePlan(s) {
		
		// construct new plan of excercise
		const min = s.bpmRange[0];
		const max = s.bpmRange[1];

    const deltaBpm = (max - min) / s.totalSteps;
    const segmentDuration = (s.exerciseDuration * 60) / (s.totalSteps + 1);
   
    // define new plan
		var segments = [];
		for (var i = 0; i <= s.totalSteps; i++) {
			segments.push({
				time:   i * segmentDuration,
				start: i * segmentDuration,
				end: i * segmentDuration + segmentDuration,
				bpm:    min + i * deltaBpm
			});
		}
		return segments;
	}

