import React, { Component } from "react";
import { Container, Row, Col, Button, Badge } from "reactstrap";

class Planner extends Component {
	state = {
		currentStep: 0,
		bars: []
	};

	timer = {
		ref: null,
		startTime: null
	};
	stepsCounter = 0;

	setPlan(plan) {
		var bars = [];

		for (var i = 0; i < plan.length; i++) {
			const bar = {
				time: plan[i].time,
				timeEnd: plan[i].end,
				segmentDuration: plan[i].end - plan[i].time,
				startTimeFormatted: this.formatTime(plan[i].start),
        		endTimeFormatted: this.formatTime(plan[i].end),
				bpm: plan[i].bpm,
				step: this.stepsCounter++
      		};

			bars.push(bar);
		}

		// update to new state
		this.stepsCounter = 0;

		this.setState(prevState => ({
			currentStep: 0,
			bars: bars
		}), () => this.planChanged());
	}

	stop() {
		if (this.timer) {
			// clear timer
			this.disposeTimer(this.timer.ref)
			// reset to first step
			
		}
		this.resetStep();
		
	}


	disposeTimer(ref) {
		if (this.timer.ref !== ref) {
			throw new Error("Wront timer.ref to dispose")			
		}
		clearInterval(this.timer.ref);
		this.timer.ref = undefined;
		this.timer.startTime = null;
		this.timer = undefined;
			
	}
	onInterval() {
		var currentStep = this.state.currentStep;
		
		// check if we're not at the end of plan
		if (currentStep + 1 >= this.state.bars.length) {
			console.log('no more steps, please stop()')
			this.disposeTimer(this.timer.ref)
			this.props.onAdvance(null)
			return;
		}
		console.log('<Planner>onInterval', this.state.currentStep + 1)
		// console.log('advancing step to' , currentStep + 1)
		this.setState({ currentStep: currentStep + 1 });
    	// console.log('returning' , this.state.currentStep)
    	this.props.onAdvance(this.state.bars[this.state.currentStep]);
	}


	start() {
		// console.log("<Planner>start()");
		// debugger
		// make sure we dispose any timer that's currently set
		if (this.timer) {
			// console.log("<Planner>startTimer() old timer disposed");
			clearInterval(this.timer.ref);
			this.timer.ref = undefined;
			this.timer.startTime = null;
			this.resetStep();
		}

		
		// calc segment length between steps and set timer for such
		const time = 1000 * (this.state.bars[this.state.currentStep].timeEnd -  this.state.bars[this.state.currentStep].time);
		var onInterval = this.onInterval.bind(this)
		// console.log('time',time)
		
		this.timer = {
			ref: setInterval(onInterval, time ),
			startTime: new Date()
		};

	}

	clear() {
		this.stepsCounter = 0;
		this.setState({ bars: [], currentStep: 0 });
		this.planChanged();
	}

	planChanged = () => {
		if (this.props.onChange) {
			// console.log("<Planner>fire onChange");
			this.props.onChange();
		}
	};

	resetStep() {
		this.setState({ currentStep: 0 });
    	this.props.onAdvance(this.state.bars[0]);
  	}
  

	getCurrentBar() {
		if (this.state.getCurrentStep >= this.state.bars.length) {
			throw new Error("we are fcked");
		}
		return this.state.bars[this.state.currentStep];
	} 

	barRender = b => {
		// let cls =
		// 	"badge badge-" +
		// 	(this.state.currentStep === b.step ? "primary" : "secondary");
		let cls = (this.state.currentStep === b.step ? "current-step" : "");
		return (
			<div className={cls} key={"key_" + b.bpm}>
				{this.formatTime(b.segmentDuration.toFixed(0))} at {b.bpm.toFixed(2)}
			</div>
		);
	};

  
	render() {
		// console.log("<Planner> render");
		// don't doo much if no plan yet
		if (this.state.bars.length === 0) {
			return <div>No plan</div>;
		}

		return (
			<Container >
				<div style={{ fontSize: "1rem" }}>
					{this.state.bars.map(bar => this.barRender(bar))}
				</div>
			</Container>
		);
  }
  
	padTime(t) {
		return t < 10 ? "0" + t : t;
	}

	formatTime(timeInSeconds) {
		//{this.leadingZeroCheck(now.getMinutes())}:{now.getSeconds()} --
		let t = new Date(0, 0, 0, 0, 0, 0, 0);
		t.setSeconds(timeInSeconds);
		let m = t.getMinutes();
		let s = t.getSeconds();
		let o = ''
		if (m != 0) {
			o += this.padTime(t.getMinutes()); 
		}
		o +=  ":" + this.padTime(t.getSeconds());
		return o;
	}
}

export default Planner;
